import React, {useState, useEffect} from 'react';

const Landing = () => {

  const [hidden, setHidden] = useState(true);
  const [color, setColor] = useState(0)

  useEffect(() => {
    setTimeout(() => {
      console.log("test", hidden);
      setHidden(false);
      console.log("test", hidden);
    }, 2000);
  }, []);

  return (
    <>
      <div id="wilder_product_page">
        <div id="div1" className={hidden ? "hidden" : ""}>
          <i className="material-icons">close</i>
        </div>
        <div id="div2" className={hidden ? "hidden" : ""}>
          <div id="black_product_image"></div>
          <div id="green_product_image"></div>
        </div>
        <div id="div3" className={hidden ? "hidden" : ""}>
          <div id="options_upper">
            <div id="size_container">
              <div id="sizes_upper">
                <p className="size_item">XS</p>
                <p className="size_item">S</p>
                <p className="size_item">M</p>
                <p className="size_item">L</p>
              </div>
              <div id="sizes_lower">
                <p className="size_item">XL</p>
                <p className="size_item">XXL</p>
              </div>
            </div>
            <div id="details_container">
              <p id="pricing">$65</p>
              <div id="details_actions">
                <button id="add_to_cart">Add To Cart</button>
                <button id="favorite"><i className="material-icons">favorite_border</i></button>
              </div>
            </div>
          </div>
          <div id="options_lower">
            <p id="description">The Wilder Pant<br/>Green</p>
            <div id="product_image"></div>
            <div id="color_picker">
              <div id="black_color_picker" className={color == 0 ? "active" : ""} onClick={() => {setColor(0)}}></div>
              <div id="green_color_picker" className={color == 1 ? "active" : ""} onClick={() => {setColor(1)}}></div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Landing;